@import 'variables';

.paragraph-style--first {
    margin: 0;
    color: #9393A3;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    letter-spacing: 0.15px;
    margin-bottom: 8px;
}

.paragraph-style--second {
    text-decoration: none;
    margin: 0;
    color: #191919;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    letter-spacing: 0.15px;
    transition-duration: $effectDuration;

    &:hover {
        color: #F98921;
    }
}