@import 'mixins';

.recommended {
    &__products {
        display: grid;
        row-gap: 40px;

        @include onTablet {
            row-gap: 56px;
            justify-items: center;
        }

        @include onDesktop {
            grid-template-columns: repeat(3, 1fr);
            column-gap: 24px;
            justify-items: stretch;
        }
    }
}