@import 'mixins';
@import 'variables';

.page {
    font-family: Manrope, sans-serif;
    font-style: normal;
    color: #1B2129;
    line-height: 140%;
    scroll-behavior: smooth;

    &:has(.page__menu:target) {
      overflow: hidden;
    }
    
    &__body {
      margin: 0;
    }
    
    &__menu {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      opacity: 0;
      pointer-events: none;
      transition: all $effectDuration;
    }
    
    &__menu:target {
      opacity: 1;
      pointer-events: all;
    }
  }