@import 'mixins';

.top-bar {
    padding-block: 24px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include onTablet {
      padding-block: 32px;
    }

    &__icons {
      display: flex;
      gap: 24px;

      @include onTablet {
        gap: 32px;
      }
    }

    &__logo-link {
      display: flex;
    }  
      
    &__logo {
        height: 16px;
       

        @include hover(transform, scale(1.2));
    
        @include onTablet {
          height: 24px;
    }
  } 
}
  